import React , {useEffect, useState, useContext, useRef} from "react";
import { AuthContext } from "../auth/Auth";
import app from "../auth/base";
import '../../assets/css/feed.css'

const FeedBox = (props) => {
    
    const [userObj, setUserObj] = useState({});
    const [authImg, setAuthImg] = useState("");

    useEffect(()=>{
        let x = "";
        const abcd = function(){           
            let t = (Date.now() - props.time)/1000;
            if(Math.floor(t/(3600*24*365))>0){               
                x = Math.floor(t/(3600*24*365)) + " yr";
            }else if(Math.floor(t/(3600*24*30))>0){
                //for month  
                x = Math.floor(t/(3600*24)) + " Month";

            }else if(Math.floor(t/(3600*24))>0){
                //for day 
                x = Math.floor(t/(3600*24)) + " day";
            }else if(Math.floor(t/3600)>0){
                //for hour 
                x = Math.floor(t/3600) + " hour";

            }else if(Math.floor(t/60)>0){
                //for minute 
                x = Math.floor(t/60) + " min";
            }else{
                //for Second 
                x = Math.floor(t) + " s";
            }
                
        }
        abcd();

        app.database().ref("users/"+props.author).on("value", (snapshot)=>{
            setUserObj({
                uid : props.author,
                content : props.content,
                authName: snapshot.val().name,
                time: x 
            });
            setAuthImg(snapshot.val().profileUrl)
            console.log(userObj.profileUrl)
        })
        
        
       
    }, [])
    

    return(
        <>
            <div className="EachFeedBox">
                <div className="authorDetails">
                    <div className="authImg">
                        <a href={"/users/"+userObj.uid}>
                            {(() => {
                                if (authImg && authImg != "") {
                                    return <img src={authImg} />;                                
                                } else{
                                    return <img src={require("../../assets/images/no-profile.png")} />;
                                }
                            })()}
                            {/* <img src={require('../../assets/images/no-profile.png')} /> */}
                        </a>
                    </div>
                    <div className="authDetails">
                        <h4><a href={"/users/"+userObj.uid}>{userObj.authName}</a></h4>
                        <p>{userObj.time} ago </p>
                    </div> 
                </div> 
                <p>{userObj.content}</p>
                <div className="_lcShreGroup">
                    <ul>
                        <li><a><i className="fa fa-thumbs-o-up"></i> <span>Like</span></a></li>
                        <li><a><i className="fa fa-comment-o"></i> <span>Comment</span></a></li>
                        <li><a><i className="fa fa-share-alt"></i> <span>Share</span></a></li>
                    </ul>
                </div>
            </div>
        </>
    )

}

export default FeedBox;
