import React, { useCallback, useContext } from "react";
import { withRouter, Redirect, Switch, Link  } from "react-router";
import app from "./base";
import { AuthContext } from "../auth/Auth";

const SignUp = ({ history }) => {

  const handleSignUp = useCallback(async event => {
    event.preventDefault();
    const { email, password, prfile_name, age } = event.target.elements;
    try {
      const userAuth = await app
        .auth()
        .createUserWithEmailAndPassword(email.value, password.value)
        .then((data)=>{
          console.log(data);
          let user = {
            name: prfile_name.value,          
            uid: data.user.uid,
            email: email.value,
            age: age.value
          }
          writeUserData(user);
        })
        
     
      function writeUserData(user) {
        app.database().ref('users/' + user.uid).set(user).then(()=>{
          history.push("/");
        }).catch(error => {
            console.log(error.message)
        });
      }

      
    } catch (error) {
      alert(error);
    }
  }, [history]);



  
  const { currentUser } = useContext(AuthContext);

  if (currentUser) {
    return <Redirect to="/" />;
  }

  return (    
      <section className="loginSection relative">
        <div className="container loginCont">
            <div className="loginInner full">
                <div className="full text-center loginTitle">
                    <h2>Sign Up</h2>
                </div>
                <form onSubmit={handleSignUp}>
                <div className="loginFormGroup">
                      <label className="logIconLabel">
                          <i className="fa fa-user icon"></i>
                      </label>
                      <input required name="prfile_name" type="text" placeholder="Prfile Name" className="loginInput"/>
                  </div>
                  <div className="loginFormGroup">
                      <label className="logIconLabel">
                          <i className="fa fa-user icon"></i>
                      </label>
                      <input required name="email" type="email" placeholder="Email" className="loginInput"/>
                  </div>
                  <div className="loginFormGroup">
                      <label className="logIconLabel">
                          <i className="fa fa-key icon"></i>
                      </label>
                      <input required name="password" type="password" placeholder="Password" className="loginInput"/>
                      <a href="#" className="showPassowrd"><i className="fa fa-eye"></i></a>                    
                  </div>
                  <div className="loginFormGroup">
                      <label className="logIconLabel">
                          <i className="fa fa-address-card-o icon"></i>
                      </label>
                      <input required name="age" type="number" placeholder="Enter Age" className="loginInput"/>
                      <a href="#" className="showPassowrd"><i className="fa fa-eye"></i></a>
                  </div>
                  <div className="text-center">
                      <button type="submit" className="loginButton">Register Now</button>
                  </div>
                </form>
                <div className=" notAmember text-center">
                    <p>Already A Member <a href="/login"> Sign In</a></p>
                </div>
            </div>
        </div>
        <div className="logInProfile bg">
            <img src={require('../../assets/images/loginCrt.png')} alt=""/>
        </div>
    </section>

  );
};

export default withRouter(SignUp);
