import React , {useEffect, useState, useContext} from "react";
import { AuthContext } from "../auth/Auth";
import app from "../auth/base";

import Header from '../laouts/header'


import  "../../assets/css/profile.css";
const Edit = ({match}) => {
    const { currentUser } = useContext(AuthContext);
    
    const [usrDtls, setUserDtls] = useState({});

    useEffect(()=>{
        const userDtsRef = app.database().ref('users/'+match.params.id);
        userDtsRef.on("value", (snp)=>{
            setUserDtls(snp.val());
            console.log(usrDtls)
        });
        
    }, [])

    const updateProfileImg = (e)=>{
        let file =e.files[0];
        var storageRef = app.storage().ref('/profile');
        // Create a reference to 'mountains.jpg'
        var mountainsRef = storageRef.child('profile.jpg');

        // Create a reference to 'images/mountains.jpg'
        var mountainImagesRef = storageRef.child('images/'+currentUser.uid);
        // Create file metadata including the content type
        var metadata = {
        contentType: 'image/jpeg',
        };
        var uploadTask = storageRef.child('images/'+currentUser.uid).put(file, metadata);

        uploadTask.on('state_changed', 
            (snapshot) => {
                // Observe state change events such as progress, pause, and resume
                // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
                var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
                console.log('Upload is ' + progress + '% done');

                // switch (snapshot.state) {
                // case app.storage.TaskState.paused: // or 'paused'
                //     console.log('Upload is paused');
                //     break;
                // case app.storage.TaskState.running: // or 'running'
                //     console.log('Upload is running');
                //     break;
                // }
            }, 
            (error) => {

            }, 
            () => {                
                uploadTask.snapshot.ref.getDownloadURL().then((downloadURL) => {
                console.log('File available at', downloadURL);
                    // console.log(usrDtls)
                    let prevVal ={}
                    app.database().ref("/users/"+match.params.id).once("value", (s)=>{
                        prevVal = s.val()
                    })
                    prevVal["profileUrl"] = downloadURL;
                    app.database().ref("/users/"+match.params.id).set(prevVal);

                });
            }
            );




        // console.log(storageRef);
        console.log()
    }
    

    return(
        <>
            <Header/>
            <section className="profileWrapper">
                <div className="profileLeft">
                    <div className="profileIdentity">
                        <div className="profileImg">
                            {(() => {
                                if (usrDtls.profileUrl && usrDtls.profileUrl != "") {
                                    return <img src={usrDtls.profileUrl} />;                                
                                } else{
                                    return <img src={require("../../assets/images/no-profile.png")} />;
                                }
                            })()}
                            <input type="file" className="profileInput" id="arProfile1" onChange={e=>updateProfileImg(e.target)}/>
                            <label htmlFor="arProfile1" className="editProfileImg"><i className="fa fa-pencil"></i></label>
                        </div>
                        
                    </div>
                    <div className="about_auth">
                        <ul>
                            {usrDtls.education &&<li><i className="fa fa-graduation-cap"></i> {usrDtls.education}</li>}
                            {usrDtls.home && <li><i className="fa fa-home"></i> Lives In <b>{usrDtls.home}</b></li>}
                            {usrDtls.lives && <li><i className="fa fa-map-marker"></i> Lives In <b>{usrDtls.lives}</b></li>}
                            {usrDtls.joined && <li><i className="fa fa-clock-o"></i> Joined <b>{usrDtls.joined}</b></li>}                            
                            {usrDtls.age && <li><i className="fa fa-user"></i> Age <b>{usrDtls.age}</b></li>}
                        </ul>
                    </div>
                    <div className="addRquestBtn">
                        <a href="#"><i className="fa fa-user-plus"></i>Add to Friend</a>
                    </div> 
                    <div className="sendMessage">
                        <a href="#"><i className="fa fa-comments"></i>Message</a>
                    </div> 
                </div> 
                <div className="profileRight">
                    <h2>Edit your Details</h2>
                    <input placeholder="Enter Name"/>
                    <input placeholder="Enter Name"/>
                    <input placeholder="Enter Name"/>
                    <input placeholder="Enter Name"/>
                    <input placeholder="Enter Name"/>
                    <input placeholder="Enter Name"/>
                    <input placeholder="Enter Name"/>
                </div> 
            </section>
        </>
    )

}

export default Edit;
