import React from "react";
import "./App.css";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Home from "./components/home/Home";
import Login from "./components/auth/Login";
import SignUp from "./components/auth/SignUp";
import { AuthProvider } from "./components/auth/Auth";
import PrivateRoute from "./components/route/PrivateRoute";

import Profile from "./components/profile/profile";
import Edit from "./components/profile/edit";
import Chat from "./components/chat/chat";
import ChatHome from "./components/chat/chat-home";
import RequestList from "./components/friend/request-list";




const App = () => {
  return (
    <AuthProvider>
      <Router>
        <div>
          <PrivateRoute exact path="/" component={Home} />
          <Route exact path="/login" component={Login} />
          <Route exact path="/signup" component={SignUp} />
          <PrivateRoute path="/users/:id" component={Profile}/>
          <PrivateRoute path="/edit/:id" component={Edit}/>
         
          <PrivateRoute exact path="/chat" component={ChatHome}/>
          <PrivateRoute exact  path="/chat/:id" component={Chat}/>

          <PrivateRoute path="/friend-requests/" component={RequestList}/>
        </div>
      </Router>
    </AuthProvider>
  );
};

export default App;
