import React, { useCallback, useContext } from "react";
import { withRouter, Redirect } from "react-router";
import app from "./base.js";
import { AuthContext } from "../auth/Auth";

import { getAuth, signInWithPopup, GoogleAuthProvider } from "firebase/auth";


const Login = ({ history },) => {

  function googleLogin(){
    


  } 
  
  const handleLogin = useCallback(
    
    async event => {
      event.preventDefault();
      const { email, password } = event.target.elements;
      try {
        await app
          .auth()
          .signInWithEmailAndPassword(email.value, password.value);
        history.push("/");
      } catch (error) {
        alert(error);
      }
    },

    [history]
  );

  const { currentUser } = useContext(AuthContext);

  if (currentUser) {
    return <Redirect to="/" />;
  }


  return (
      <section className="loginSection relative">
        <div className="container loginCont">
            <div className="loginInner full">
                <div className="full text-center loginTitle">
                    <h2>Login</h2>
                </div>
                <form onSubmit={handleLogin}>
                  <div className="loginFormGroup">
                      <label className="logIconLabel">
                          <i className="fa fa-user icon"></i>
                      </label>
                      <input name="email" type="email" placeholder="username" className="loginInput"/>
                  </div>
                  <div className="loginFormGroup">
                      <label className="logIconLabel">
                          <i className="fa fa-key icon"></i>
                      </label>
                      <input name="password" type="password" placeholder="Password" className="loginInput"/>
                      <a href="#" className="showPassowrd"><i className="fa fa-eye"></i></a>
                      <a href="javascript:;" className="forgotPass">Forgot Password</a>
                  </div>
                  <div className="text-center">
                      <button type="submit" className="loginButton">Login</button>
                  </div>
                </form>
                <div className=" notAmember text-center">
                    <p>Not a member?
                        <a href="/signup">Sign up now</a>
                    </p>
                </div>
                <div className="orGooglSingsec m-t-10">
                    <div className="lrginOrHeading">
                        <span>
                            Or
                        </span>
                    </div>
                    <ul className="d-flex">
                        <li><a onClick={googleLogin} className="arGooglesignbtn">Continue with Google</a>
                        </li>
                        <li><a href="#" className="fbSignin">Continue with Facebook</a></li>
                    </ul>
                </div>
            </div>
        </div>
        <div className="logInProfile bg">
            <img src={require('../../assets/images/loginCrt.png')} alt=""/>
        </div>
    </section>


  );
};

export default withRouter(Login);
