import React , {useEffect, useState, useContext, useRef} from "react";
import app from "../auth/base";
import { AuthContext } from "../auth/Auth";
import FeedBox from "./feed";
import Header from '../laouts/header'


const Home = () => {
  const {currentUser} = useContext(AuthContext);
  const [feed, setFeed] = useState('');
  const [allFeed, setAllfeed] = useState([]);
  // const [allFeed, setAllfeed] = useState([]);

  const inputRef = React.useRef();


  const feedPost = ()=>{
    var database = app.database();
    
    database.ref('posts/').push({
        aUid: currentUser.uid,
        acontent: feed,
        time: Date.now(),
      }).then(()=>{
        inputRef.current.value="";
        // console.log(inputRef)
        // inputRef
        alert("success");
      });
    
  }

  useEffect((previousList)=>{
    const commentsRef = app.database().ref('posts/');
    
    commentsRef.on('value', function(data) {   
      let getVal = data.val();
      
      let arr = [];

      for(let keys in getVal){
        let obj = {};
        obj["userId"] = getVal[keys].aUid;
        obj["postContent"] = getVal[keys].acontent;
        obj["time"] = getVal[keys].time;
        arr.push(obj); 
      }
      console.log(getVal);
      setAllfeed(arr); 
  });



  }, [])



  return (
    <>
      <Header/>
      <section className="__feed_Sect">
        <div className="container">
          <div className="whats_on_your_mind">
            <h4>Write Something to Post: </h4>
            <textarea rows="5" className="feedTextArea" ref={inputRef} onChange={event => setFeed(event.target.value)}></textarea>
            <div className="Submit_wrap">
              <button className="postButton" onClick={feedPost}>Post</button>
            </div>
          </div> 
          <div className="postedData">
              {allFeed.map((index, key)=>(
                <FeedBox key={key} author={index.userId} content={index.postContent} time={index.time}/>                
              ))}


          </div>
          

        </div>
      </section>
      
    </>
  );
};

export default Home;
