import React , {useEffect, useState, useContext, useRef} from "react";
import { AuthContext } from "../auth/Auth";
import app from "../auth/base";
import '../../assets/css/feed.css';
import './chat.css'
import Header from '../laouts/header';


const Chat = () => {
    const {currentUser} = useContext(AuthContext);
    const [profile, setProfile] = useState({});
    const db = app.database();
    const [chat, setChat] = useState({});

    let [arHtml, setHtml] = useState("")

    const sentChat = ()=>{       
        const chatRef = db.ref("/chat");
        chatRef.push({
         ...chat,
         to: "7v07Kecx8ZcWM8NRqhCkod4JsiC2",
         from: currentUser.uid
        });        
    }

    useEffect(()=>{
        db.ref("/users/"+currentUser.uid).on("value", (e)=>{
            setProfile(e.val());
        });

        
        var chatRef = app.database().ref('chat/');
        chatRef.on('value', function(data) {
            let x="";
            var myUserId = currentUser.uid;
            var to = "7v07Kecx8ZcWM8NRqhCkod4JsiC2";
            var xx= data.val();
            console.log(xx)
            for(let key in xx){
                let i = xx[key];

                if(i.from == myUserId && i.to == to ){  
                    x += `<div class="message parker">${i.chat}</div>`;
                }else if(i.to == myUserId && i.from == to){
                    x += `<div class="message stark">${i.chat}</div>`;               
                }else{
                    x = "";
                }
            }
           
            console.log(arHtml+x);
            setHtml(arHtml+x)
                   
        });
        


    }, []);

    function createMarkup() {
        console.log(arHtml)
        return {__html: arHtml};
      }    



    

    return(
        <>
            <Header/>
            <div className="welcomeNote">        
            <div className="center">
                <div className="welcomeNoneInr">
                    <div className="proflePicturear">
                        {(() => {
                                if (profile.profileUrl && profile.profileUrl != "") {
                                    return <img src={profile.profileUrl} />;                                
                                } else{
                                    return <img src={require("../../assets/images/no-profile.png")} />;
                                }
                            })()}
                        <p id="welcomNote">{profile.name}</p>
                    </div>
                
                    {/* <a href="javascript: logOut();" className="logOutBtn">Log Out</a> */}
                </div>
                <div className="contacts">
                <h2>
                    Contacts
                </h2>
                <div className="contactInr">                  
                
                <div className="contact contactList" contact-uid="7v07Kecx8ZcWM8NRqhCkod4JsiC2">
                        <div className="pic">
                            <img src={require("../../assets/images/no-profile.png")}/>
                        </div>
                        <div className="name">
                            Spider
                        </div>
                        <div className="message" id="lastMsg">
                            ...
                        </div>
                    </div></div>
                </div>
                <div className="chat">
                <div className="contact bar">
                    <div className="pic">
                        {/* <img src="images/noimage.png"> */}
                    </div>
                    <div className="name" id="senderName" data-email="dipankarsarkar26@gmail.com">
                    Tony Stark
                    </div>
                    <div className="seen">
                    Today at 12:56
                    </div>
                </div>
                <div className="messages" id="chat">
                    <div className="time">
                    Today at 11:41
                    </div>
                
                    <div dangerouslySetInnerHTML={createMarkup()} />
                </div>
                <div className="input">
                    <i className="fa fa-camera"></i>
                    <i className="fa fa-microphone"></i>                    
                    <input placeholder="Type your message here!" type="text" onKeyPress={e=>{setChat({chat: e.target.value, time: Date.now()})}}/>
                    <i className="fa fa-paper-plane" onClick={sentChat}></i>
                </div>
                </div>
            </div>

        </div>
        </>
    )

}

export default Chat;
